// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios'; //主要AJAX套件
import VueAxios from 'vue-axios';//將它轉為VUE的套件
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap';
import VeeValidate from 'vee-validate';
import zhTWValidate from 'vee-validate/dist/locale/zh_TW';//新增繁體中文

import App from './App';
import router from './router';
import './bus';
import currencyFilter from './filters/currency';

axios.defaults.withCredentials = true;
Vue.use(VueAxios, axios);//用use才能執行上面兩行
Vue.use(VeeValidate);
VeeValidate.Validator.localize('zh.TW', zhTWValidate);

Vue.config.productionTip = false;

Vue.component('Loading',Loading);
Vue.filter('currency',currencyFilter);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
});

router.beforeEach((to, from, next) => {
  // to and from are both route objects. must call `next`.
  if (to.meta.requiresAuth){
    const api = `${process.env.APIPATH}/api/user/check`; 
        axios.post(api).then((response) => {
        console.log(response.data);
        if(response.data.success){
           next();
        }else{
          next({
            path: '/login'
          });
        }
    });
  }else{
    next();
  }
});



